
.registration-form {
    background-color: #000000da;
    color: white;
    padding: 30px;
    border-radius: 10px;
    max-width: 600px;
    margin: 0 auto;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  
 
  .form-title {
    font-size: 24px;
    text-align: center;
    color: #ffd700; 
    margin-bottom: 20px;
  }
  
  
  .form-row {
    margin-bottom: 15px;
  }
  
    .form-row label {
    display: block;
    font-size: 16px;
    margin-bottom: 5px;
  }
  
 
  .form-row input {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #333;
    color: white;
    font-size: 16px;
  }
  
  
  .form-row input:focus {
    outline: none;
    border: 1px solid #ffd700;
  }
  
 
  .radio-group {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
  }
  
  .radio-group label {
    font-size: 14px;
    color: #ddd;
  }
  
  
  .submit-btn {
    background-color: #ffd700;
    color: black;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    width: 100%;
  }
  
  .submit-btn:hover {
    background-color: #e6c200;
  }
  
